            me.close();
            me.toList();
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        axios
          .post(
            "payment/add",
            {
              liquidate: this.liquidate,
              loan: this._id,
              date: this.date,
              transaction_date: moment(new Date()).local().format("YYYY-MM-DD"),
              total: this.total,
              receipt: this.receipt,
              glosa: this.glosa,
              registry: this.registry,
            },
            config
          )
          .then(function (response) {
            me.close();
            me.toList();
          })
          .catch(function (error) {
            console.log(error);
          });
      }
      this.hideEditPayments();
      this.dialogAmort2 = false;
      this.dialogAmort = false;
    },
    saveLoan() {
      let me = this;
      let header = { token: this.$store.state.token };
      let config = { headers: header };
      if (this.validate()) {
        return;
      }
      if (this.editedIndex > -1) {
        axios
          .put(
            "loan/update",
            {
              _id: this._id,
              code: this.code,
              voucher: this.voucher,
              request_date: this.request_date,
              debt_date: this.debt_date,
              amount_requested: this.amount_requested,
              loan_term: this.loan_term,
              amortization: this.amortization,
              interest_rate: this.interest_rate,
            },
            config
          )
          .then(function (response) {
            me.clear();
            me.close();
            me.toList();
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        //
      }
      this.close();
    },
    addInt(id) {
      console.log(id);
      let me = this;
      let header = { token: this.$store.state.token };
      let config = { headers: header };
      if (this.validate()) {
        return;
      }
      axios
        .get("loan/queryint?_id=" + id, config)
